
.titles {
  font-size: 26px;
  color: #00B4BC;
  letter-spacing: 6.93px;
  height: 36px;
  line-height: 36px;
  position: relative;
  width: 398px;
  margin: 0 auto;
  margin-bottom: 22px;
  border-bottom: 6px solid #ff8500;
}
.titles span {
    position: absolute;
    display: block;
    width: 100%;
    left: 0;
    top: 12px;
}
.titles i {
    position: absolute;
    display: block;
    width: 120px;
    height: 6px;
    left: 50%;
    top: 35px;
    background: #ff8500;
    opacity: 0.5;
    margin-left: -30px;
}
.desc {
  margin-bottom: 52px;
  font-size: 20px;
  color: #FC4083;
}
.banner {
  width: 100%;
  height: calc(100vw * 558px / 1920px);
  max-height: 558px;
  background-color: #6E4756;
}
.banner img {
    display: block;
    width: 100%;
    max-width: 1920px;
    margin: 0 auto;
}
.product_description {
  padding: 30px 0;
  background: #F5FFFF;
  padding-bottom: 80px;
}
.product_description > * {
    max-width: 1200px;
    margin: 0 auto;
}
.product_description h4 {
    margin-bottom: 52px;
}
.product_description .features {
    position: relative;
    height: 469px;
    width: 553px;
    margin: 0 auto;
}
.product_description .features img {
      width: 100%;
      display: block;
}
.product_description .features p {
      position: absolute;
      font-size: 16px;
      color: #787878;
      line-height: 24px;
      width: 388px;
      text-align: left;
}
.joinUs {
  background: #4CC0C4;
  padding: 60px 0 169px;
  position: relative;
}
.joinUs > span {
    position: absolute;
}
.joinUs > span:first-child {
    width: 200px;
    height: 150px;
    bottom: 0;
    left: 50%;
    margin-left: -750px;
    background-image: url(../images/index.png);
    background-position: 0 -1649px;
}
.joinUs > span:nth-child(2) {
    width: 200px;
    height: 21px;
    bottom: 120px;
    left: 50%;
    margin-left: -540px;
    background-image: url(../images/index.png);
    background-position: 0 -1323px;
}
.joinUs > span:nth-child(3) {
    width: 200px;
    height: 33px;
    bottom: 70px;
    left: 50%;
    margin-left: 520px;
    background-image: url(../images/index.png);
    background-position: 0 -1389px;
}
.joinUs h2, .joinUs h4 {
    color: #fff;
}
.joinUs .idol_recruit {
    position: relative;
}
.joinUs .idol_recruit span:first-child {
      display: inline-block;
      height: 143.4px;
      width: 282px;
      background-image: url(../images/index.png);
      background-position: 0 -1168px;
}
.joinUs .idol_recruit span:last-child {
      position: absolute;
      left: 50%;
      top: 75px;
      width: 58px;
      height: 21px;
      margin-left: 400px;
      background-image: url(../images/index.png);
      background-position: 0 -1323px;
}
.joinUs .join_way {
    overflow: hidden;
    max-width: 1200px;
    margin: 0 auto;
    padding-top: 38px;
    position: relative;
}
.joinUs .join_way > span {
      position: absolute;
      left: 50%;
      top: 15px;
      margin-left: 400px;
      width: 110px;
      height: 79px;
      background-image: url(../images/index.png);
      background-position: 0 -3293px;
}
.joinUs .join_way .left, .joinUs .join_way .right {
      float: left;
      padding-top: 60px;
}
.joinUs .join_way .left {
      width: 50%;
}
.joinUs .join_way h2 {
      font-size: 24px;
      height: 36px;
      line-height: 36px;
      position: relative;
      width: 408px;
      margin: 0 auto;
      margin-bottom: 6px;
      border-bottom: 6px solid #FC4083;
}
.joinUs .join_way h2 span {
        position: absolute;
        display: block;
        width: 100%;
        left: 0;
        top: 12px;
}
.joinUs .join_way h4 {
      font-size: 18px;
}
.joinUs .join_way .right {
      background: #FFFFFF;
      border: 7px solid #94D8DB;
      color: #00B4BC;
      padding: 21px 31px;
      width: 458px;
      text-align: left;
      box-sizing: border-box;
}
.joinUs .join_way .right h3 {
        font-size: 24px;
        padding-bottom: 14px;
        border-bottom: 1px solid #00B4BC;
}
.joinUs .join_way .right .recruit_condition {
        overflow: hidden;
        font-size: 20px;
        padding: 24px 0;
}
.joinUs .join_way .right .recruit_condition .condition_left {
          float: left;
          margin-right: 29px;
}
.joinUs .join_way .right .recruit_condition .condition_right {
          float: left;
}
.joinUs .join_way .right .recruit_condition .condition_right li {
            padding-left: 47px;
            background-image: url(../images/index.png);
            background-position: 7.5px -1545px;
}
.joinUs .join_way .right .recruit_condition .condition_right li:not(:first-child) {
              margin-top: 16px;
}
.joinUs .join_way .right .contact_email span:first-child {
        background: #00B4BC;
        border-radius: 41px;
        display: inline-block;
        width: 69px;
        height: 32px;
        line-height: 32px;
        text-align: center;
        margin-right: 43px;
        color: #fff;
}
.joinUs .join_way .right .contact_email span:last-child {
        font-size: 25px;
        color: #FC4083;
}
.joinUs p {
    text-align: left;
    max-width: 810px;
    margin: 0 auto;
    line-height: 30px;
    font-size: 14px;
}
.videos {
  position: relative;
  background: #FFF6FA;
  padding: 42px 0 58px;
}
.videos > span {
    position: absolute;
}
.videos > span:first-child {
    left: 50%;
    top: 90px;
    width: 58px;
    height: 21px;
    margin-left: 550px;
    background-image: url(../images/index.png);
    background-position: 0 -1353px;
}
.videos > span:nth-child(2) {
    left: 50%;
    top: 750px;
    width: 58px;
    height: 21px;
    margin-left: -750px;
    background-image: url(../images/index.png);
    background-position: 0 -1353px;
}
.videos > span:nth-child(3) {
    left: 50%;
    top: 0;
    width: 140px;
    height: 70px;
    margin-left: 580px;
    background-image: url(../images/index.png);
    background-position: 0 -2120px;
}
.videos > span:nth-child(4) {
    left: 50%;
    bottom: 0;
    width: 169px;
    height: 100px;
    margin-left: 500px;
    background-image: url(../images/index.png);
    background-position: 0 -1886px;
}
.videos h2 {
    color: #FB0730;
    letter-spacing: 0;
    width: 100px;
    border-bottom: 6px solid #FFD300;
}
.videos h2 span {
      position: absolute;
      display: block;
      width: 100%;
      left: 0;
      top: 12px;
}
.videos h4 {
    color: #00B4BC;
}
.videos .videos_list {
    max-width: 1200px;
    margin: 0 auto;
    overflow: hidden;
}
.videos .videos_list li {
      float: left;
      padding-bottom: 42px;
      text-align: left;
}
.videos .videos_list li:not(:nth-child(3n - 2)) {
        margin-left: 54px;
}
.videos .videos_list li a {
        display: block;
}
.videos .videos_list li a img {
          width: 360px;
          height: 202.5px;
          margin-bottom: 17px;
          display: block;
}
.videos .videos_list li a .video_title {
          font-size: 20px;
          color: #00B4BC;
}
.videos .videos_list li a .video_desc {
          font-size: 16px;
          color: #666666;
}
.videos .more_video {
    width: 229px;
    height: 69px;
    line-height: 61px;
    padding-left: 61px;
    box-sizing: border-box;
    margin: 0 auto;
    border: 4px solid #FB0730;
    font-size: 26px;
    color: #FB0730;
    background-image: url(../images/index.png);
    background-position: 43px -1570px;
}
.company_profile {
  background-color: #64BDE3;
  padding: 58px 0 70px;
  color: #fff;
  position: relative;
}
.company_profile h2 {
    color: #fff;
    letter-spacing: 0;
    margin-bottom: 22px;
    border-color: #277597;
    width: 180px;
}
.company_profile p {
    font-size: 18px;
    line-height: 34px;
    margin-bottom: 67px;
}
.company_profile .company_desc {
    overflow: hidden;
    max-width: 1120px;
    margin: 0 auto;
}
.company_profile .company_desc span {
      float: left;
      background-image: url(../images/index.png);
}
.company_profile .company_desc span:first-child {
      width: 445px;
      height: 435px;
      background-position: 0 -2390px;
}
.company_profile .company_desc span:last-child {
      float: right;
      width: 529px;
      height: 240px;
      background-position: 0 -2860px;
      margin-top: 95px;
}
@media screen and (max-width: 980px) {
.videos .videos_list {
    max-width: 1200px;
}
.videos .videos_list li:not(:nth-child(3n - 2)) {
      margin-left: 30px;
}
.videos .videos_list li a {
      width: 300px;
}
.videos .videos_list li a img {
        width: 100%;
}
.product_description .features {
    width: 380px;
}
.product_description .features p {
      width: 250px;
}
.product_description .features p:nth-child(2) {
      left: -260px !important;
      top: 100px !important;
}
.product_description .features p:nth-child(3) {
      left: -230px !important;
      bottom: 100px !important;
}
.product_description .features p:nth-child(4) {
      right: -140px !important;
      top: 0px !important;
}
.product_description .features p:nth-child(5) {
      right: -280px !important;
      top: 90px !important;
}
.product_description .features p:nth-child(6) {
      right: -280px !important;
      bottom: 120px !important;
}
}
@media screen and (max-width: 980px) {
.videos_list li {
    width: 100% !important;
}
.videos_list li a {
      width: 100% !important;
}
}



