
@import url(../styles/reset.css);
body {
  position: relative;
  overflow: hidden;
}
.activeVideos {
  border-bottom: 4px solid #666;
}
.activeIdol {
  border-bottom: 4px solid #0092D1;
}
.header {
  position: fixed;
  top: 0;
  border-top-width: 4px;
  border-top-style: solid;
  -o-border-image: linear-gradient(90deg, #FD4F66 2%, #E34079 22%, #910FB5 44%, #6451CC 64%, #00E5FF 85%, #00AFC4 100%) 30 30;
     border-image: linear-gradient(90deg, #FD4F66 2%, #E34079 22%, #910FB5 44%, #6451CC 64%, #00E5FF 85%, #00AFC4 100%) 30 30;
  width: 100%;
  overflow: hidden;
  box-sizing: border-box;
  height: 80px;
  padding: 10px 0;
  box-shadow: 0 2px 4px 0 rgba(208, 208, 208, 0.5);
  z-index: 1;
  background: #fff;
}
.header > .header_content {
    max-width: 1120px;
    margin: 0 auto;
    overflow: hidden;
}
.header .logo {
    float: left;
    width: 56px;
    height: 56px;
    display: block;
    margin-right: calc(23.5px/2);
    background: url(../images/index.png);
}
.header ul {
    float: left;
    color: #fff;
}
.header ul li {
      float: left;
      height: 56px;
      line-height: 56px;
      font-size: 20px;
      padding: 0 calc(23.5px/2);
}
.header .language {
    float: right;
    border: 1px solid #FC4083;
    border-radius: 100px;
    width: 145px;
    overflow: hidden;
    margin-top: 10px;
}
.header .language a {
      color: #FC4083;
      float: left;
      display: block;
      line-height: 26px;
      height: 26px;
      margin: 2px;
      text-align: center;
}
.header .language a:first-child {
        width: 60px;
}
.header .language a:last-child {
        width: 77px;
}
.header .language a.active {
      background: #FC4083;
      border-radius: 100px;
      color: #fff;
}
.footer {
  background: #3A525C;
  padding: 34px 0 0;
}
.footer > * {
    max-width: 1120px;
    margin: 0 auto;
    text-align: center;
}
.footer a {
    color: #fff;
}
.footer .links {
    overflow: hidden;
    margin-bottom: 26px;
}
.footer .links .logo {
      background-image: url(../images/index.png);
      display: block;
      float: left;
      width: 103px;
      height: 60px;
      font-size: 24px;
      line-height: 60px;
      padding-left: 64px;
}
.footer .links .facebook, .footer .links .twitter {
      float: right;
      width: 51px;
      height: 51px;
      background-image: url(../images/index.png);
      background-position: 0 -3224px;
}
.footer .links .twitter {
      margin-right: 43.2px;
      background-image: url(../images/index.png);
      background-position: 0 -3164px;
}
.footer ul {
    overflow: hidden;
}
.footer ul li {
      float: left;
}
.footer ul li:not(:first-child) {
        margin-left: 59px;
}
.footer ul li a {
        display: block;
        width: 104px;
        height: 28px;
        line-height: 28px;
        padding-left: 24px;
        background-image: url(../images/index.png);
        background-position: 0 -1618px;
        text-align: left;
}
.footer .desc {
    color: #fff;
    margin-top: 20px;
    margin-bottom: 0;
    height: 30px;
    line-height: 30px;
    text-align: center;
    font-size: 12px;
}
.footer .desc a {
      display: inline-block;
}
.footer .desc:last-child {
      padding-bottom: 30px;
}
.main {
  text-align: center;
  position: fixed;
  top: 80px;
  overflow-y: auto;
  overflow-x: hidden;
  width: 100%;
  height: calc(100vh - 80px);
}
::-webkit-scrollbar {
  width: 6px;
}
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 8px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
}
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #FC4083;
  -webkit-box-shadow: inset 0 0 8px #FC4083;
}
::-webkit-scrollbar-thumb:window-inactive {
  background: #FC4083;
}
::-webkit-scrollbar-track-piece {
  width: 6px;
}
::-webkit-resizer {
  width: 6px;
}



