/* 重置样式 start */
body,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
p,
blockquote,
dl,
dt,
dd,
ul,
ol,
li,
pre,
fieldset,
lengend,
button,
input,
textarea,
th,
td {
    /* table elements 表格元素 */
    margin: 0;
    padding: 0;
}

ol,
ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

a {
    text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: normal;
}

address,
cite,
dfn,
em,
var {
    font-style: normal;
}
i {
    font-style: normal;
}

/* 将斜体扶正 */

img,
input {
    border: 0 none;
}

div,
a {
    outline: none
}

a {
    text-decoration: none;
}

.hide {
    display: none
}

html,body {
    width: 100vw;
    height: 100vh;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    -webkit-overflow-scrolling: iscroll;
    line-height: 1.5;
    color: rgb(61, 80, 95);
    font-family: 'Microsoft YaHei';
}

a:hover {
    opacity: 0.8;
}

/* 重置样式 end */



